<template>
  <div class="gray-switch">
    <el-form 
      v-loading="loading"
      ref="form" 
      :model="form" 
      label-width="80px">
      <el-form-item label="">
      <el-switch 
        v-model="form.graySwitch"
        @change="switchChange"
        ></el-switch>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      title: 'gray swithc',
      loading: true,
      form: {
        graySwitch: false
      }
    }
  },
  methods: {
    switchChange() {
      console.log(this.form.graySwitch)
      try {
      chrome.storage.sync.set({
        __FILEZ_GRAY_FLAG__: this.form.graySwitch
      })
    }catch(e) {
      console.error(e)
    }
    }
  },
  created() {
    console.log('created gray-switch ', )
    try {
      chrome.storage.sync.get(['__FILEZ_GRAY_FLAG__'], (res) => {
        this.form.graySwitch = res.__FILEZ_GRAY_FLAG__
        this.loading = false
      })
    }catch(e) {
      this.loading = false
      console.error(':', e)
    }
  }
}
</script>

